export class User {
    
    public $key: string; // como o firebase utiliza ele automaticamente, no precisa por ele no construtor

    constructor(
        public name: string,
        public username: string,
        public email: string,
        // public password: string,
        // public uid: string, // como no precisamos conhecer a senha do usurio, substituimos a senha pelo ID
        public photo: string,
    ) {};

}


